import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the ProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

declare var device:any;
declare var facebookConnectPlugin: any;

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage { 

  userDataProfile;

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider,public events: Events,public zone: NgZone, public storage: Storage) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProfilePage');

    this.storage.get('usr_tok_by').then((value) => {
      this.userDataProfile = value
    })
  }

  goProfile() {
    this.navCtrl.push('CuentaPage');
  }
  goAyuda() {
    this.navCtrl.push('AjustesPage');
  }

  logOut(pagina){ 
    console.log(pagina);
     
    this.apiProvider.cerrarS({idCliente:this.userDataProfile.idCliente, device:device?device.uuid:''})
    .then(data => {
    console.log(data);
    });
  
   this.events.publish('userLogout');
    this.events.publish('loginOK');
    
  facebookConnectPlugin.logout(res => {
      //  this.events.publish('userLogout');
      }, e => {
      console.log('Error logout from Facebook', e);
       // this.events.publish('userLogout');
        });
    } 

}



